<template>
  <div class="container">
    <div class="scroll">
      <div class="block" v-for="(item,idx) in data" :key="idx">
        <div class="title">{{idx+1 +' '+item.title}}</div>
        <div class="content" v-for="(i,index) in item.model" :key="index">{{i.label}}</div>
      </div>
    </div>
    <div class="text" v-if="connected && !data.length">连接成功</div>
    <div class="text" v-if="error && !data.length">{{error}}</div>
  </div>
</template>

<script>
import SDK from "../assets/NIM_Web_SDK_v6.5.0";
export default {
  name: "Receiver",
  data() {
    return {
      connected: false,
      error: false,
      data: []
    };
  },
  mounted() {
    const _this = this;
    let nim = SDK.NIM.getInstance({
      appKey: "d8201c00ad3022a6bae5d14954b30605",
      account: "b62275218",
      token: "6d251e4e84965f4fcf10b097be3600c5",
      onconnect: function() {
        console.log("SDK连接成功");
        _this.connected = true;
      },
      onerror: function(err) {
        console.log(err);
        _this.error = String(err);
      },
      oncustomsysmsg: function(sysMsg) {
        console.log("收到系统通知", sysMsg);
        _this.data = JSON.parse(sysMsg.content);
      }
    });
  },
  methods: {}
};
</script>

<style scoped>
.container {
  padding: 0.4rem 0.3rem;
  font-size: 0.3rem;
  overflow: scroll;
  color: #fff;
  position: relative;
  height: 85%;
}
.scroll {
  position: absolute;
  top: 0;
  left: 0;
}
.content {
  font-size: 0.3rem;
  padding: 0.1rem;
  transition: all 1s ease-in-out;
}

.text {
  color: #fff;
}
</style>



